<template>
  <div class="page-container">
    <Loader v-if="loader" />
    <div class="wrap-1500">
      <div id="course-list">
        <div class="category-wrap" :class="{'on': categoryOn}">
          <div class="cur-item" @click="categoryOn = !categoryOn">請選課程</div>
          <ul class="reset">
            <li
              v-for="category in ['全部課程', '入門課程', '中階課程', '高階課程']"
              :key="category"
              :class="{'active': selectedCategory === category}"
              @click="selectCategory(category)"
            >
              {{ category }}
            </li>
          </ul>
        </div>

        <div v-for="category in ['入門課程', '中階課程', '高階課程']" :key="category">
          <section v-if="selectedCategory === category || selectedCategory === '全部課程'">
            <div class="ti-box text-center" data-aos="ani-text">
              <div class="ti">
                <span v-for="char in category">{{ char }}</span>
                <!-- {{ category }} -->
              </div>
            </div>
            <div class="wrap-1300">
              <div class="course-item-g">
                <CourseItem v-for="item in courses[category]" :key="item.id" :item="item" :level="item.category"></CourseItem>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Loader from '@/components/Loader.vue';
  import CourseItem from '@/components/frontend/CourseItem.vue';
  export default {
    components:{
      Loader,
      CourseItem,
    },
    data(){
      return{
        products: [],
        loader: false,
        status:{
          loadingItem: '',
        },
        courses: {
          "入門課程": [],
          "中階課程": [],
          "高階課程": [],
        },
        selectedCategory: '全部課程',
        categoryOn: false,
      }
    },
    methods:{
      getProducts(page=1){
        const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/products/?page=${page}`;
        this.loader = true;
        this.$http.get(api)
        .then((res) => {
          const data = res.data.products;
          data.forEach((e) => {
            const category = e.category;
            if (Object.prototype.hasOwnProperty.call(this.courses, category)) {
              this.courses[category].push(e);
            }
          });
          this.loader = false;
        })
      },
      addCart(id){
        const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
        const cart = {
          product_id: id,
          qty: 1,
        };
        this.status.loadingItem = id;
        this.$http.post(api,{data:cart}).then((res) => {
          this.status.loadingItem = '';
          this.getCart();
        });
      },
      selectCategory(category) {
        this.selectedCategory = category;
        if(window.innerWidth <= 767){
          this.categoryOn = false;
        }
      },
    },
    created(){
      this.getProducts();
    },
  }
</script>

