  margin-bottom: 40px;
  ul{
    font-size: 0;
    text-align: center;
    li{
      display: inline-block;
      vertical-align: top;
      font-size: 18px;
      padding: 10px;
      border-radius: 10px;
      border: 2px solid $color-brown;
      margin-right: 10px;
      transition: .2s ease-in-out;
      cursor: pointer;
      &:last-child{ margin-right: 0; }
      &.active{ 
        color: #fff; 
        background: $color-orange;
      }
    }
  }
}
@media (min-width: 992px) {
  .category-wrap{
    .cur-item{ display: none; }
    ul li:hover{
      color: #fff; 
      background: $color-orange;
    }
  }
}
@media (max-width: 767px) {
  .category-wrap{
    position: relative;
    &.on{
      .cur-item::after{
        top: 20px;
        transform: rotate(-45deg);
      }
      ul{
        opacity: 1;
        pointer-events: auto;
      }
    }
    .cur-item{ 
      display: block;
      font-size: 18px;
      width: 100%;
      padding: 10px;
      text-align: center;
      background: $color-orange;
      color: #fff;
      border-radius: 10px;
      border: 2px solid $color-brown;
      position: relative;
      &::after{
        content: '';
        display: block;
        width: 10px;
        height: 10px;
        border-top: 2px solid #fff;
        border-right: 2px solid #fff;
        position: absolute;
        top: 17px;
        right: 10px;
        transition: .15s ease-in-out;
        transform: rotate(45deg);
      }
    }
    ul{
      width: 100%;
      position: absolute;
      top: 51px;
      left: 0;
      background: #fff;
      z-index: 10;
      border-radius: 10px;
      overflow: hidden;
      transition: .15s ease-in-out;
      pointer-events: none;
      opacity: 0;
      li{
        width: 100%;
        border: none;
        border-radius: 0;
        &.active{
          color: $color-orange;
          background: none;
        }
      }
    }
  }
  #course-list .course-item-g{
    .course-item{ width: 50%; }
  }
}
@media (max-width: 599px) {
  #course-list .course-item-g{
