<template>
  <div class="course-item">
    <div class="box" :class=getClassForLevel()>
      <router-link @click="scrollToTop" :to="`course/${item.id}`" class="pic">
        <i></i>
        <img :src="item.imageUrl" alt="Course">
      </router-link>
      <div class="txt">
        <div class="course-name text-center">{{ item.title }}</div>
        <div class="level-box">
          <p>{{ item.category }}</p>
          <span>適合：{{ item.unit }}</span>
        </div>
        <div class="price-box">
          <div class="old">NT${{ item.origin_price }}</div>
          <div class="new">NT${{ item.price }}</div>
        </div>
        <div class="btn-wrap d-flex justify-content-center">
          <router-link :to="`course/${item.id}`" title="詳細課程" class="btn-style" @click="scrollToTop"><span>詳細課程</span><i class="bi bi-arrow-right"></i></router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['item','level'],
    methods:{
      getClassForLevel(){
        if (this.level === '入門課程') {
          return 'levelOne';
        } else if (this.level === '中階課程') {
          return 'levelTwo';
        } else if (this.level === '高階課程') {
          return 'levelThree';
        }
        return '';
      },
      scrollToTop() {
        window.scrollTo(0, 0);
      },
    },
  }
